<template>
  <div>
    <h2>SortableJS</h2>
    <div>
      <el-button type="primary" @click="getSortedData()"></el-button>
      <ul class="w300" id="sortable">
        <li class="flex" v-for="item in dataList" :key="item.id">
          <div><el-checkbox v-model="item.checked"></el-checkbox> <span>{{item.text}}</span></div>
          <div class="handle">
            <i class="el-icon-s-operation"></i>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Sortable from "sortablejs";
export default {
  name: "mySortable",
  data() {
    return {
      checked: false,
      dataList: [{
        text: '1',
        id: '1',
        checked: false,
      }, {
        text: '2',
        id: '2',
        checked: false,
      }, {
        text: '3',
        id: '3',
        checked: false,
      }, {
        text: '4',
        id: '4',
        checked: false,
      }, {
        text: '5',
        id: '5',
        checked: false,
      }, {
        text: '6',
        id: '6',
        checked: false,
      }, ]
    };
  },

  methods: {
    // 
    getSortedData() {
      const that = this;
      const list = that.dataList.map(value => value.id);

      that.$message(`${list.toString()}`);
    }
  },

  mounted() {
    const that = this;
    var dom = document.getElementById('sortable');
    console.log(Sortable)
    new Sortable(dom, {
      handle: '.handle', // handle's class
      animation: 150,
      onEnd: function (evt) {  //
      console.log(evt);
      // oldIndexnewIndex, 
      // dataList
      // 
      
      that.dataList.splice(evt.newIndex, 0, that.dataList.splice(evt.oldIndex, 1)[0]);
      that.$nextTick(function () {
        // 
        that.dataList = [...that.dataList];
      });
    },
    });
  }
};
</script>

